<template>
  <a-layout class="choiceStore">
    <choiceHeader class="choiceStore-header"></choiceHeader>
    <mycontent :isChoiceStore="isChoiceStore"></mycontent>
    <myfooter></myfooter>
  </a-layout>
</template>

<script>
import choiceHeader from './components/header'
import choiceList from './components/list'
import mycontent from '../layout/mycontent'
import myfooter from '../layout/myfooter'
import {checkLogin} from '../../common/js/checkLogin'
export default {
  name: 'choiceStore',
  components: {
    choiceHeader,
    choiceList,
    mycontent,
    myfooter
  },
  data () {
    return {
      tab: '1',
      isChoiceStore: true
    }
  },
  created () {
    this.checkMyLogin()
  },
  mounted () {
  },
  methods: {
    callback (key) {
      console.log(key)
      this.tab = key
    },
    checkMyLogin () {
      // 判断是否登录
      if (!checkLogin()) {
        this.$router.replace({
          name: 'login',
          query: {
          }
        })
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
.choiceStore
  height 100vh
  background-color #f0f2f5
</style>



