<template>
  <a-layout-header class="choiceHeader">
    <span class="trigger">小聪云店</span>
    <span class="choiceHeader-right" @click="loginMyOut">退出系统</span>
    <span class="choiceHeader-right">欢迎 {{userName}}</span>
  </a-layout-header>
</template>

<script>
import {loginOut} from '../../../common/js/checkLogin'
export default {
  name: 'choiceHeader',
  data () {
    return {
      userName: localStorage.userId && localStorage.userId !== 'undefined' ? localStorage.userId : ''
    }
  },
  methods: {
    loginMyOut () {
      // 退出系统
      let that = this
      loginOut(function () {
        that.$router.replace({
          path: '/login',
          query: {
          }
        })
      })
    }
  }
}
</script>

<style lang="stylus" scoped>
.choiceHeader
  background #fff
  padding 0
  .trigger
    font-size 18px
    color #000
    font-weight bold
    margin-left 40px
  .choiceHeader-right:hover
    color #1890ff
  .choiceHeader-right
    float right
    font-weight bold
    cursor pointer
    margin-right 40px
</style>



